import React, { useState } from 'react'
import Layout from '../../components/layout/Layout'
import '../../style/Register.css';
import {toast} from 'react-hot-toast';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

const Register=()=> {
    const [name,setName] =useState('');
    const[email,setEmail]=useState('');
    const [password,setPassword]=useState('');
    const [phone,setPhone]=useState('');
    // const [address,setAddress]=useState('');
    // const [answer, setAnswer]=useState("");
const navigate = useNavigate();
   const handleSubmit = async (e)=>{
    e.preventDefault()
    try {
      const res = await axios.post(`/api/v1/auth/register`,
      {name,email,password,phone}
      );
      if(res.data.success){
        toast.success(res.data.message)
        navigate('/login')
      }else{
        toast.error(res.data.message)
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong")
    }
   }
  return (
    <Layout title={"Register"}>


   <div className="form-container ">
   <form onSubmit={handleSubmit}>
   <h4 className="title">REGISTER FORM</h4>
  <div className="mb-3">
   
    <input type="text" value={name} onChange={(e)=>setName(e.target.value)} className="form-control" id="exampleInputname" placeholder='Name' required />
    
  </div>
  <div className="mb-3">

    <input type="email" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control" id="exampleInputemail" placeholder='email' required/>
    
  </div>
  <div className="mb-3">
    <input type="password" value={password} onChange={(e)=>setPassword(e.target.value)} className="form-control" id="exampleInputPassword1" placeholder='password' required/>
  </div>
  <div className="mb-3">
      <input type="text" value={phone} onChange={(e)=>setPhone(e.target.value)} className="form-control" id="exampleInputPhone"  placeholder='phone' required/>
  </div>

   {/* <div className="mb-3">
      <input type="text" value={address} onChange={(e)=>setAddress(e.target.value)} className="form-control" id="exampleaddress" placeholder='address' required/>
  </div>
  <div className="mb-3">
      <input type="text" value={answer} onChange={(e)=>setAnswer(e.target.value)} className="form-control" id="exampleaddress" placeholder='Faviouret vacation place?' required/>
  </div> */}

  <button type="submit" className="btn btn-primary">Register</button>
</form>
</div>

    </Layout>
  )
}

export default Register