import { useState, useEffect } from "react";
import Layout from "../components/layout/Layout";
import axios from "axios";
import { Link } from "react-router-dom";
import { openDB } from "idb";
import Search from "../Assets/search.png";
import Village from "../Assets/village.png";
import List from "../Assets/list.png";
import Family from "../Assets/family.png";
const HomePage = () => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const localData = await getLocalData();
      if (localData && localData.length > 0) {
        setData(localData);
      } else {
        // console.log("hi");
        const response = await axios.get(`/api/v1/product/allvoter`);
        console.log(response);
        const newData = response.data;
        console.log(newData);
        setData(newData);
        saveToLocalData(newData);
      }
    } catch (error) {
      console.error("Error fetching data:", error);
      setError("An error occurred while fetching data.");
    }
  };

  const dbName = "rajasthan-voters-db"; // Updated database name

  const getLocalData = async () => {
    try {
      const db = await openDB(dbName, 1, {
        upgrade(db) {
          db.createObjectStore("votes");
        },
      });
      const tx = db.transaction("votes", "readonly");
      const store = tx.objectStore("votes");
      return store.getAll();
    } catch (error) {
      console.error("Error retrieving local data:", error);
      return []; // Return empty array if an error occurs
    }
  };
  
  const saveToLocalData = async (newData) => {
    try {
      if (!newData) return;
  
      console.log("Saving data to IndexedDB:", newData);
  
      const db = await openDB(dbName, 1, {
        upgrade(db) {
          db.createObjectStore("votes");
        },
      });
      const tx = db.transaction("votes", "readwrite");
      const store = tx.objectStore("votes");
      store.clear();
  
      if (Array.isArray(newData)) {
        newData.forEach((item, index) => {
          store.put(item, index + 1);
        });
      }
      await tx.done;
    } catch (error) {
      console.error("Error saving data to IndexedDB:", error);
    }
  };
  

  return (
    <Layout title={"Rajasthan Voters App"}>
      <div className="container-fluid mt-3">
        <div className="row">
          <div className="col-md-3 col-6 p-2">
            <Link to="/voterList" className="card-link">
              <div className="card">
                <div className="card-body">
                  <div className="card-img">
                    <img src={Search} alt="family List" />
                  </div>
                  <h4 className="card-title mt-2">Search</h4>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-md-3 col-6 p-2">
            <Link to="/villages" className="card-link">
              <div className="card">
                <div className="card-body">
                  <div className="card-img">
                    <img src={Village} alt="family List" />
                  </div>
                  <h4 className="card-title mt-2">Villages</h4>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-md-3 col-6 p-2">
            <Link to="/check" className="card-link">
              <div className="card">
                <div className="card-body">
                  <div className="card-img">
                    <img src={List} alt="family List" />
                  </div>
                  <h4 className="card-title mt-2">All List</h4>
                </div>
              </div>
            </Link>
          </div>
          <div className="col-md-3 col-6 p-2">
            <Link to="/family" className="card-link">
              <div className="card">
                <div className="card-body">
                  <div className="card-img">
                    <img src={Family} alt="family List" />
                  </div>
                  <h4 className="card-title mt-2">Family</h4>
                </div>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default HomePage;
