import React, { useState, useEffect } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";
import Datetime from "react-datetime";
import "react-datetime/css/react-datetime.css";
import Layout from "../components/layout/Layout";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";
import { openDB } from "idb";

const VoterDetails = () => {
  const [voterDetails, setVoterDetails] = useState({});
  const [dob, setDob] = useState(null);
  const [mobileNo, setMobileNo] = useState("");
  const [voted, setVoted] = useState(false);
  const [died, setDied] = useState(false);
  const [submittedDob, setSubmittedDob] = useState(null);
  const [submittedMobileNo, setSubmittedMobileNo] = useState("");
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [surveyOption, setSurveyOption] = useState("");
  const navigate = useNavigate();
  const { slug } = useParams();
  const auth = JSON.parse(localStorage.getItem("auth"));
  const userRole = auth?.user?.role;

  // Check network status
  const isOnline = navigator.onLine;

  useEffect(() => {
    const fetchVoterDetails = async () => {
      try {
        if (!slug) {
          console.error("Slug parameter is undefined");
          return;
        }
    
        // Attempt to fetch voter details from IndexedDB
        const localData = await getLocalData(slug);
    
        if (localData) {
          setVoterDetails(localData);
          setDob(new Date(localData.DOB)); // Parse the date string
          setMobileNo(localData.MOBILE_NO || "");
          setVoted(localData.voted || false);
          setDied(localData.died || false);
        } else {
          console.log("Local data not found, fetching from the server...");
          // Fetch voter details from the server only when online
          if (isOnline) {
            const response = await axios.get(`/api/v1/product/allvotes/${encodeURIComponent(slug)}`);
            const data = response.data;
    
            setVoterDetails(data);
            setDob(new Date(data.DOB)); // Parse the date string
            setMobileNo(data.MOBILE_NO || "");
            setVoted(data.voted || false);
            setDied(data.died || false);
    
            // Update IndexedDB with new data
            await updateLocalData(slug, data);
          }
        }
    
        setLoading(false);
      } catch (error) {
        console.error("Error fetching voter details:", error);
        setError(error.message);
      }
    };
    

    fetchVoterDetails();
  }, [slug, isOnline]); // Include isOnline in dependency array

  useEffect(() => {
    // Set the default value of surveyOption when the component mounts
    if (voterDetails && voterDetails.surveyOption) {
      setSurveyOption(voterDetails.surveyOption);
    }
  }, [voterDetails]);

  const handleDateChange = (date) => {
    setDob(date);
  };

  const handleMobileChange = (e) => {
    const inputMobileNo = e.target.value;
    const mobileNumber = inputMobileNo.replace(/\D/g, ""); // Remove non-digit characters
    if (mobileNumber.length <= 10) {
      setMobileNo(mobileNumber);
    }
  };

  const handleVotedChange = () => {
    setVoted(!voted);
  };

  const handleDiedChange = () => {
    setDied(!died);
  };

  const handleSurveyOptionChange = (option) => {
    setSurveyOption(option);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const data = {
        DOB: dob ? dob.toISOString() : null,
        MOBILE_NO: mobileNo,
        voted: voted,
        died: died,
        surveyOption: surveyOption, // Include survey option in the data
      };

      if (isOnline) {
        const response = await axios.put(
          `/api/v1/product/allvotes/${encodeURIComponent(slug)}`,
          data
        );

        if (response.status === 200) {
          setSubmittedDob(dob);
          setSubmittedMobileNo(mobileNo);
          setVoterDetails(response.data);
          toast.success("Updated Successfully");
          setTimeout(() => {
            navigate("/");
          }, 2000);
        } else {
          console.error("Update failed:", response.data);
          setError("Update failed");
        }
      } else {
        await updateLocalData(slug, data);
        toast.success("Update saved locally");
      }
    } catch (error) {
      console.error("Error updating details:", error);
      setError(error.message);
    }
  };

  const getLocalData = async (slug) => {
    console.log("Getting local data for slug:", slug);
    try {
      const db = await openDB("rajasthan-voters-db", 1);
      console.log("Opened database successfully:", db);
      const tx = db.transaction("votes", "readonly");
      console.log("Opened transaction successfully:", tx);
      const store = tx.objectStore("votes");
      console.log("Opened object store successfully:", store);
      const data = await store.get(slug);
      console.log("Retrieved data:", data);
      return data;
    } catch (error) {
      console.error("Error getting local data:", error);
      setError(error.message);
      return null;
    }
  };
  

  const updateLocalData = async (slug, newData) => {
    try {
      const db = await openDB("rajasthan-voters-db", 1);
      const tx = db.transaction("votes", "readwrite");
      const store = tx.objectStore("votes");
      await store.put(newData, slug);
      await tx.complete;
    } catch (error) {
      console.error("Error updating local data:", error);
      setError(error.message);
    }
  };

  return (
    <Layout title={`Voter: ${encodeURIComponent(slug)}`}>
      <div className="container mt-4">
        <div className="row justify-content-center">
          <div className="col-md-8">
            <div className="card">
              <div className="card-header">Voter Details</div>
              <div className="card-body">
                {error && <p>Error: {error}</p>}
                <p>Name: {voterDetails.FM_NAME_EN}</p>
                <p>Last Name: {voterDetails.LASTNAME_EN}</p>
                <p>Part No.: {voterDetails.PART_NO}</p>
                <p>Village: {voterDetails.C_HOUSE_NO}</p>
                <p>
                  Age/Gender: {voterDetails.AGE} {voterDetails.GENDER}
                </p>
                <p>EPIC No: {voterDetails.EPIC_NO}</p>
                <form onSubmit={handleSubmit}>
                  <div className="form-group">
                    <label>D.O.B:</label>
                    <Datetime
                      inputProps={{
                        id: "dob",
                        className: "form-control",
                      }}
                      value={dob}
                      onChange={handleDateChange}
                    />
                  </div>
                  <div className="form-group">
                    <label htmlFor="mobileNo">Mobile No.:</label>
                    <input
                      type="number"
                      id="mobileNo"
                      className="form-control"
                      value={mobileNo}
                      onChange={handleMobileChange}
                      readOnly={userRole === 0}
                    />
                  </div>
                  <div className="form-group">
                    <div className="form-check">
                      <input
                        type="checkbox"
                        id="voted"
                        className="form-check-input"
                        checked={voted}
                        onChange={handleVotedChange}
                        disabled={userRole === 0}
                      />
                      <label htmlFor="voted" className="form-check-label">
                        Voted
                      </label>
                    </div>
                  </div>
                  <div className="form-group">
                    <div className="form-check">
                      <input
                        type="checkbox"
                        id="died"
                        className="form-check-input"
                        checked={died}
                        onChange={handleDiedChange}
                        disabled={userRole === 0}
                      />
                      <label htmlFor="died" className="form-check-label">
                        Died
                      </label>
                    </div>
                  </div>
                  <div className="form-group">
                    <label htmlFor="surveyOption">Survey Option:</label>
                    <select
                      id="surveyOption"
                      className="form-control"
                      value={surveyOption}
                      onChange={(e) => handleSurveyOptionChange(e.target.value)}
                    >
                      <option value="">Select an option</option>
                      <option value="Ours">Ours</option>
                      <option value="Opposite">Option 2</option>
                      <option value="Doubt">Doubt</option>
                      <option value="Others">Others</option>
                    </select>
                  </div>
                  {userRole !== 0 && (
                    <button type="submit" className="btn btn-primary">
                      Update Details
                    </button>
                  )}
                  {submittedDob && submittedDob instanceof Date && (
                    <p>
                      Updated Date of Birth: {submittedDob.toDateString()}
                    </p>
                  )}
                  {submittedMobileNo && (
                    <p>Updated Mobile No.: {submittedMobileNo}</p>
                  )}
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default VoterDetails;
