import React, { useEffect, useState } from "react";
import Layout from "../components/layout/Layout";
import axios from "axios";
import { Link } from "react-router-dom";

const FamilyList = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalFamily, setTotalFamily] = useState(0);
  const pageSize = 100;
  const [totalPages, setTotalPages] = useState(0);

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (navigator.onLine) {
          const response = await axios.get(
            `/api/v1/product/family?page=${currentPage}`
          );
          console.log("API response:", response.data); // Log response for debugging
          setData(response.data.data);
          setTotalFamily(response.data.rlnCount); // Update total families count
          setLoading(false);
        } else {
          const offlineData = await fetchDataFromIndexedDB();
          setData(offlineData);
          setTotalFamily(offlineData.length);
          setLoading(false);
        }
      } catch (error) {
        console.error("Error fetching data:", error);
        setError("An error occurred while fetching data.");
        setLoading(false);
      }
    };

    fetchData();
  }, [currentPage]);

  const fetchDataFromIndexedDB = async () => {
    return new Promise((resolve, reject) => {
      const request = window.indexedDB.open("rajasthan-voters-db", 1);

      request.onerror = (event) => {
        console.error("IndexedDB error:", event.target.error);
        reject("An error occurred while accessing IndexedDB.");
      };

      request.onsuccess = (event) => {
        const db = event.target.result;
        const transaction = db.transaction("votes", "readonly");
        const objectStore = transaction.objectStore("votes");

        const data = {};

        objectStore.openCursor().onsuccess = (event) => {
          const cursor = event.target.result;
          if (cursor) {
            const RLN_FM_NM_EN = cursor.value.RLN_FM_NM_EN;
            if (!data[RLN_FM_NM_EN]) {
              data[RLN_FM_NM_EN] = 1;
            } else {
              data[RLN_FM_NM_EN]++;
            }
            cursor.continue();
          } else {
            resolve(
              Object.entries(data).map(([RLN_FM_NM_EN, count]) => ({
                RLN_FM_NM_EN,
                count,
              }))
            );
          }
        };

        objectStore.openCursor().onerror = (event) => {
          console.error("IndexedDB cursor error:", event.target.error);
          reject("An error occurred while fetching data from IndexedDB.");
        };
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains("votes")) {
          const objectStore = db.createObjectStore("votes", {
            keyPath: "id",
            autoIncrement: true,
          }); // Ensure a unique ID for each entry
          objectStore.createIndex("RLN_FM_NM_EN", "RLN_FM_NM_EN", {
            unique: true,
          }); // Index on RLN_FM_NM_EN
          // Add other indexes or modify structure as needed
        }
      };
    });
  };

  const totalPage = Math.ceil(totalFamily / pageSize);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  // Function to generate an array of page numbers
  const generatePageNumbers = () => {
    const pageNumbers = [];
    if (totalPage <= 5) {
      for (let i = 1; i <= totalPage; i++) {
        pageNumbers.push(i);
      }
    } else {
      const startPage = Math.max(1, currentPage - 2);
      const endPage = Math.min(totalPage, currentPage + 2);

      if (startPage > 1) {
        pageNumbers.push(1, "...");
      }

      for (let i = startPage; i <= endPage; i++) {
        pageNumbers.push(i);
      }

      if (endPage < totalPage) {
        pageNumbers.push("...", totalPage);
      }
    }
    return pageNumbers;
  };

  // Function to calculate serial number
  const calculateSerialNumber = (index) => {
    return (currentPage - 1) * pageSize + index + 1;
  };

  return (
    <Layout title={"All Villages"}>
      <div className="container-fluid mt-3">
        <div className="row">
          {loading ? (
            <div className="col-md-12 text-center">
              <h2>Loading...</h2>
            </div>
          ) : error ? (
            <div className="col-md-12 text-center">
              <h2>{error}</h2>
            </div>
          ) : data && data.length > 0 ? (
            <div className="col-md-12">
              <p>Total number of Families: {totalFamily}</p>

              <div className="border shadow table-responsive">
                <table className="table table-hover table-striped">
                  <thead>
                    <tr>
                      <th scope="col">S.No.</th>
                      <th scope="col">Village</th>
                      <th scope="col">Total Voters</th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((item, index) => (
                      <tr key={index}>
                        <td>{calculateSerialNumber(index)}</td>

                        <td>
                          <Link
                            to={`/family/${encodeURIComponent(
                              item.RLN_FM_NM_EN
                            )}`}
                          >
                            {item.RLN_FM_NM_EN}
                          </Link>
                        </td>
                        <td>{item.count}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
              {/* Pagination */}
              <nav aria-label="Page navigation example">
                <ul className="pagination">
                  {/* Previous button */}
                  <li
                    className={`page-item ${
                      currentPage === 1 ? "disabled" : ""
                    }`}
                  >
                    <button
                      className="page-link"
                      onClick={() => handlePageChange(currentPage - 1)}
                    >
                      Previous
                    </button>
                  </li>

                  {/* Page numbers */}
                  {generatePageNumbers().map((pageNumber, index) => (
                    <li
                      key={index}
                      className={`page-item ${
                        pageNumber === currentPage ? "active" : ""
                      }`}
                    >
                      {pageNumber === "..." ? (
                        <span className="page-link">...</span>
                      ) : (
                        <button
                          className="page-link"
                          onClick={() => handlePageChange(pageNumber)}
                        >
                          {pageNumber}
                        </button>
                      )}
                    </li>
                  ))}

                  {/* Next button */}
                  <li
                    className={`page-item ${
                      currentPage === totalPages ? "disabled" : ""
                    }`}
                  >
                    <button
                      className="page-link"
                      onClick={() => handlePageChange(currentPage + 1)}
                    >
                      Next
                    </button>
                  </li>
                </ul>
              </nav>
            </div>
          ) : (
            <div className="col-md-12 text-center">
              <h2>No data available</h2>
            </div>
          )}
        </div>
      </div>
    </Layout>
  );
};

export default FamilyList;
