import React, { useEffect, useState, useRef } from "react";
import Layout from "../components/layout/Layout";
import axios from "axios";
import { Link } from "react-router-dom";

const VillageList = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalVillages, setTotalVillages] = useState(0);
  const pageSize = 100;
  const containerRef = useRef(null);
  const [fetching, setFetching] = useState(false);

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  useEffect(() => {
    const handleScroll = () => {
      if (
        containerRef.current &&
        window.innerHeight + window.scrollY >=
          containerRef.current.offsetTop + containerRef.current.offsetHeight
      ) {
        handleLoadMore();
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [data, currentPage]);

  const fetchData = async () => {
    try {
      if (navigator.onLine) {
        const response = await axios.get(
          `/api/v1/product/village?page=${currentPage}`
        );
        console.log("API response:", response.data); // Log response for debugging
        setData((prevData) => [...prevData, ...response.data.data]);
        setTotalVillages(response.data.partnoCount);
        setLoading(false);
      } else {
        const offlineData = await fetchDataFromIndexedDB();
        setData((prevData) => [...prevData, ...offlineData]);
        setTotalVillages(offlineData.length);
        setLoading(false);
      }
    } catch (error) {
      console.error("Error fetching data:", error);
      setError("An error occurred while fetching data.");
      setLoading(false);
    }
  };

  const fetchDataFromIndexedDB = async () => {
    return new Promise((resolve, reject) => {
      const request = window.indexedDB.open("rajasthan-voters-db", 1);

      request.onerror = (event) => {
        console.error("IndexedDB error:", event.target.error);
        reject("An error occurred while accessing IndexedDB.");
      };

      request.onsuccess = (event) => {
        const db = event.target.result;
        const transaction = db.transaction("votes", "readonly");
        const objectStore = transaction.objectStore("votes");

        const data = {};

        objectStore.openCursor().onsuccess = (event) => {
          const cursor = event.target.result;
          if (cursor) {
            const PART_NO = cursor.value.PART_NO;
            if (!data[PART_NO]) {
              data[PART_NO] = 1;
            } else {
              data[PART_NO]++;
            }
            cursor.continue();
          } else {
            resolve(
              Object.entries(data).map(([PART_NO, count]) => ({
                PART_NO,
                count,
              }))
            );
          }
        };

        objectStore.openCursor().onerror = (event) => {
          console.error("IndexedDB cursor error:", event.target.error);
          reject("An error occurred while fetching data from IndexedDB.");
        };
      };

      request.onupgradeneeded = (event) => {
        const db = event.target.result;
        if (!db.objectStoreNames.contains("votes")) {
          const objectStore = db.createObjectStore("votes", {
            keyPath: "id",
            autoIncrement: true,
          }); // Ensure a unique ID for each entry
          objectStore.createIndex("PART_NO", "PART_NO", { unique: true }); // Index on PART_NO
          // Add other indexes or modify structure as needed
        }
      };
    });
  };

  const handleLoadMore = () => {
    setCurrentPage((prevPage) => prevPage + 1);
  };

  return (
    <Layout title={"All Villages"}>
      <div className="container-fluid mt-3" ref={containerRef}>
        <div className="row">
          {loading ? (
            <div className="col-md-12 text-center">
              <h2>Loading...</h2>
            </div>
          ) : error ? (
            <div className="col-md-12 text-center">
              <h2>{error}</h2>
            </div>
          ) : data && data.length > 0 ? (
            <div className="col-md-12">
              <p>Total number of villages: {totalVillages}</p>

              <div className="border shadow table-responsive">
                <table className="table table-hover table-striped">
                  <thead>
                    <tr>
                      <th scope="col">S.No.</th>
                      <th scope="col">Village</th>
                      <th scope="col">Total Voters</th>
                    </tr>
                  </thead>
                  <tbody>
                    {data.map((item, index) => (
                      <tr key={index}>
                        <td>{index + 1}</td>
                        <td>
                          <Link
                            to={`/village/${encodeURIComponent(item.PART_NO)}`}
                          >
                            {item.PART_NO}
                          </Link>
                        </td>
                        <td>{item.count}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          ) : (
            <div className="col-md-12 text-center">
              <h2>No data available</h2>
            </div>
          )}
        </div>
      </div>
      {fetching && <h2 className="text-center">Loading more...</h2>}
    </Layout>
  );
};

export default VillageList;
