import React, { useState, useEffect, useRef } from "react";
import Layout from "../components/layout/Layout";
import axios from "axios";
import { Link } from "react-router-dom";
import { openDB } from "idb";

const Check = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [totalCount, setTotalCount] = useState(0);
  const [fetching, setFetching] = useState(false);
  const itemsPerPage = 1000;
  const containerRef = useRef(null);

  useEffect(() => {
    fetchData();
  }, [currentPage]);

  useEffect(() => {
    const handleScroll = () => {
      if (
        containerRef.current &&
        containerRef.current.scrollTop + containerRef.current.clientHeight >=
          containerRef.current.scrollHeight &&
        !fetching &&
        data.length < totalCount
      ) {
        setFetching(true);
      }
    };

    if (containerRef.current) {
      containerRef.current.addEventListener("scroll", handleScroll);
    }

    return () => {
      if (containerRef.current) {
        containerRef.current.removeEventListener("scroll", handleScroll);
      }
    };
  }, [fetching, data.length, totalCount]);

  useEffect(() => {
    const fetchDataOnScroll = async () => {
      if (fetching) {
        setCurrentPage((prevPage) => prevPage + 1);
      }
    };

    fetchDataOnScroll();
  }, [fetching]);

  const fetchData = async () => {
    setLoading(true);
    try {
      let fetchedData;
      let totalCount;
      if (navigator.onLine) {
        const response = await axios.get(
          `/api/v1/product/allvotes?page=${currentPage}`
        );
        fetchedData = response.data.data;
        totalCount = response.data.totalCount;
      } else {
        const { data, totalCount: dbTotalCount } = await fetchDataFromIndexedDB(
          currentPage,
          itemsPerPage
        );
        fetchedData = data;
        totalCount = dbTotalCount;
      }
      setData((prevData) => [...prevData, ...fetchedData]);
      setTotalCount(totalCount);
      setLoading(false);
      setFetching(false);
    } catch (error) {
      console.error("Error fetching data:", error);
      setError("An error occurred while fetching data.");
      setLoading(false);
      setFetching(false);
    }
  };

  const fetchDataFromIndexedDB = async (page, pageSize) => {
    try {
      const db = await openDB("rajasthan-voters-db", 1);
      if (!db.objectStoreNames.contains("votes")) {
        throw new Error("Object store 'votes' not found in the database.");
      }
      const tx = db.transaction("votes", "readonly");
      const store = tx.objectStore("votes");

      // Fetch data in smaller batches to improve performance
      const offset = (page - 1) * pageSize;
      const limit = pageSize;
      const data = await store.getAll(undefined, limit, offset);

      // Get total count of items in the store
      const totalCountRequest = store.count();
      const totalCount = await totalCountRequest;

      return { data, totalCount };
    } catch (error) {
      console.error("Error fetching data from IndexedDB:", error);
      throw error;
    }
  };

  const calculateSerialNumber = (index) => {
    return index + 1;
  };

  return (
    <Layout title={"Rajasthan Voters App"}>
      <div
        className="container-fluid mt-3"
        ref={containerRef}
        style={{ height: "600px", overflowY: "scroll" }}
      >
        <div className="row">
          <div className="col-md-12">
            {loading && <h2 className="text-center">Loading...</h2>}
            {error && <h2 className="text-center">{error}</h2>}
            {data.length > 0 && (
              <>
                <p className="mt-2">Total number of items: {totalCount}</p>
                <div className="border shadow table-responsive">
                  <table className="table table-hover table-striped">
                    <thead>
                      <tr>
                        <th scope="col">S.No.</th>
                        <th scope="col">Name</th>
                        <th scope="col">Village</th>
                        <th scope="col">EPIC No</th>
                      </tr>
                    </thead>
                    <tbody>
                      {data.map((item, index) => (
                        <tr key={index}>
                          <td>{calculateSerialNumber(index)}</td>
                          <td>
                            <Link
                              to={`/voter/${encodeURIComponent(item.FM_NAME_EN)}`}
                            >
                              {item.FM_NAME_EN}
                            </Link>
                          </td>
                          <td>{item.C_HOUSE_NO}</td>
                          <td>{item.EPIC_NO}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              </>
            )}
            {data.length === 0 && !loading && (
              <h2 className="text-center">No data found</h2>
            )}
          </div>
        </div>
      </div>
      {fetching && <h2 className="text-center">Loading more...</h2>}
    </Layout>
  );
};

export default Check;
